@import url(https://fonts.googleapis.com/css?family=Poppins:400,700&display=swap);
body {
  background: #2d2d2d;
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.main {
  visibility: hidden; }
  .main .title-container {
    position: absolute;
    top: 350px;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    overflow: hidden;
    border-bottom: 2px solid #646464;
    padding: 2px 11px; }
    .main .title-container .title {
      color: #c6c6c6;
      margin: 0;
      font-size: 16px;
      font-weight: 600;
      letter-spacing: 5px; }
  .main .container {
    width: 960px;
    margin: 0 auto;
    height: 100vh;
    display: flex;
    align-items: center; }
    .main .container .img-container {
      width: 960px;
      height: 600px;
      position: relative;
      overflow: hidden; }
      .main .container .img-container .img-overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        right: 0;
        background: #2d2d2d; }
      .main .container .img-container img {
        width: 960px;
        margin: 0 auto; }

